import blog from '@/api/blog'

export default {
    data() {
        return {
            index:'',
            blogs: [],
            user: {},
            page: 1,
            total:0
        }
    },
    created() {
        //如何获取用户id：在路由 router 中 path: '/user/:userId' 存放用户的id
        //使用params方式传递路由参数 方法：this.$route.params.name
        this.userId = this.$route.params.userId
        //判断this.$route.query.page是否存在
        this.page = this.$route.query.page || 1
        blog.getBlogsByUserId(this.userId, { page: this.page })
            .then(res => {
                // console.log(res)
                this.blogs = res.data
                this.page = res.page
                this.total = res.total
                //由于后台没有获取user的接口,可以通过博客data中user信息获取
                if (res.data.length > 0) { //必须博客存在才能获取user信息
                    this.user = res.data[0].user
                }
            })

        //精确：获取指定用户的博客
        /*
        getBlogsByUserId(userId, { page=1, atIndex } = { page: 1}) {
          return this.getBlogs({ userId, page, atIndex })
        }
        */
        
    },
    methods: {
        onPageChange(newPage) {
         blog.getBlogsByUserId(this.userId,{page:newPage}) //精确：获取指定页数博客列表，只得到显示在首页的博客
            .then(res => {
                this.blogs = res.data  //数据存放到blog
                this.total = res.total //数据替换total
                this.page = res.page   //数据替换page
                this.$router.push( {path:`/user/${this.userId}`,query:{ page:newPage } } )
            })

        },

        splitDate(dataStr) {
            let dateObj = typeof dataStr === 'object' ? dataStr : new Date(dataStr)
            return {
                date: dateObj.getDate(),
                month: dateObj.getMonth() + 1,
                year: dateObj.getFullYear()
            }
        }
    }
}


